import BlackFridayBanner from "@/components/BlackFridayBanner";
import ProductsView from "@/components/ProductsView";
import { getAllCategories } from "@/sanity/lib/products/getAllCategories";
import { getAllProducts } from "@/sanity/lib/products/getAllProducts";
import { HeroSlider } from "./hero/page";
import { PhotoCollage } from "./collage/page";
import Footer from "./footer/page";
export const dynamic = "force-static";
export const revalidate = 3600;

export default async function Home() {
  const products = await getAllProducts();
  const categories = await getAllCategories();

  // Debug Products Rendering
  // console.log(
  //   crypto.randomUUID().slice(0, 5) +
  //     `>>> Rendered the product page cache for ${products.length} products
  //     and ${categories.length} categories`
  // );

  return (
    <div>
      <HeroSlider />
      <div className="flex flex-col items-center justify-top min-h-screen bg-bg-yellow-50 p-4">
        <ProductsView products={products} categories={categories} />
      </div>
      <BlackFridayBanner />
      <PhotoCollage />
      <Footer />
    </div>
  );
}
